"use client";
import { useState } from "react";
import { Upload, DollarSign, Tag, CalendarDays } from "lucide-react";
import { motion } from "framer-motion";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";

export default function Subastar() {
  const [imagen, setImagen] = useState<File | null>(null);

  return (
    <section className="min-h-screen bg-gradient-to-b from-blue-100 via-white to-pink-100 flex flex-col items-center justify-center px-6 py-16">
      <motion.h1
        initial={{ opacity: 0, y: -40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="text-5xl md:text-6xl font-extrabold text-blue-600 mb-4 text-center"
      >
        ¡Publica tu Subasta!
      </motion.h1>

      <p className="text-lg md:text-xl text-gray-600 mb-10 text-center max-w-2xl">
        Ingresa los detalles del producto que deseas subastar y deja que otros usuarios compitan por él.
      </p>

      <Card className="w-full max-w-lg shadow-2xl rounded-2xl backdrop-blur-md bg-white/70">
        <CardContent className="p-8 space-y-6">
          {/* Nombre del producto */}
          <div>
            <label className="flex items-center gap-2 text-gray-700 font-semibold">
              <Tag size={20} />
              Nombre del producto
            </label>
            <input
              type="text"
              placeholder="Ejemplo: Laptop ASUS Gamer"
              className="w-full mt-2 p-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-400 outline-none"
            />
          </div>

          {/* Precio inicial */}
          <div>
            <label className="flex items-center gap-2 text-gray-700 font-semibold">
              <DollarSign size={20} />
              Precio inicial
            </label>
            <input
              type="number"
              placeholder="Ejemplo: 500000"
              className="w-full mt-2 p-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-400 outline-none"
            />
          </div>

          {/* Fecha de cierre */}
          <div>
            <label className="flex items-center gap-2 text-gray-700 font-semibold">
              <CalendarDays size={20} />
              Fecha de cierre
            </label>
            <input
              type="date"
              className="w-full mt-2 p-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-400 outline-none"
            />
          </div>

          {/* Imagen del producto */}
          <div>
            <label className="flex items-center gap-2 text-gray-700 font-semibold">
              <Upload size={20} />
              Imagen del producto
            </label>
            <input
              type="file"
              accept="image/*"
              onChange={(e) => setImagen(e.target.files?.[0] || null)}
              className="w-full mt-2 text-gray-600"
            />
            {imagen && (
              <img
                src={URL.createObjectURL(imagen)}
                alt="Vista previa"
                className="w-full h-48 object-cover mt-3 rounded-xl shadow-md"
              />
            )}
          </div>

          <Button className="w-full mt-6 bg-blue-600 hover:bg-blue-700 text-white text-lg rounded-xl py-3 transition">
            Publicar Subasta
          </Button>
        </CardContent>
      </Card>
    </section>
  );
}
